.wrapper {
  padding-bottom: 0px; }

.burger {
  margin: 0px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 1s ease;
  border: none;
  background: transparent; }
  .burger:focus {
    outline: none; }
  .burger .cta {
    height: 50px;
    width: 50px;
    cursor: pointer;
    border: none; }

.toggle-btn {
  height: 2px;
  width: 25px;
  background-color: #1A1C22;
  position: relative;
  top: 50%;
  left: 0;
  margin: auto;
  transition: all 0.3s ease-in-out; }

.toggle-btn:before {
  content: "";
  height: 2px;
  width: 25px;
  box-shadow: 0 -10px 0 0 #1A1C22;
  position: absolute;
  top: 0;
  left: 0;
  transition: all 0.3s ease-in-out; }

.toggle-btn:after {
  content: "";
  height: 2px;
  width: 25px;
  box-shadow: 0 10px 0 0 #1A1C22;
  position: absolute;
  top: 0;
  left: 0;
  transition: all 0.3s ease-in-out; }

.active .toggle-btn {
  background-color: transparent;
  transition: all 0.2s ease-in-out; }

.active .toggle-btn:before {
  top: 0;
  transition: all 0.3s ease-in-out; }

.active .toggle-btn:after {
  top: 0;
  transition: all 0.3s ease-in-out; }

.active .type11 {
  transform: rotate(90deg);
  transition: all 0.4s ease-in-out; }

.active .type11:before {
  transform: translateY(0) rotate(40deg); }

.active .type11:after {
  transform: translateY(0) rotate(-40deg); }

#wrapper-navbar {
  background: transparent !important;
  width: 100%; }
  #wrapper-navbar.navbar-home {
    background: transparent !important; }
    #wrapper-navbar.navbar-home #main-logo .cls-1 {
      fill: #fff; }
    #wrapper-navbar.navbar-home .navbar.bg-primary {
      border-bottom: 2px solid transparent;
      background: transparent !important; }
    #wrapper-navbar.navbar-home .line-gradient {
      z-index: 100;
      position: absolute; }
  #wrapper-navbar #main-logo {
    height: 62px;
